@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #F7F5E9;
  font-family: 'Patrick Hand', 'Helvetica' ;
  font-size: x-large
}
.drawingApp {
  display: grid;
  grid-template-columns: auto;
}

canvas {
  border: 10px ridge goldenrod;
  background-color: white;
  margin-top: 20px;
}

.canvasContainer {
 text-align: center;
}

button {
  text-align: left;
  border: 2px solid black;
  border-radius: 50%;
  padding: 20px;
  margin: 5px;
}

button:hover {
  cursor: pointer;
}

.tools, .instructions {
  padding-left: 25vw;
}

.buttons {
  padding: 10px;
  text-align: left;
}

.icons {
  border-width: 2px;
  border-radius: 10%;
  padding: 10px;
}

.btn-erase {
  background-color: palevioletred;
  color: white;
  border-style: outset;
  border: 2px outset palevioletred;
}

.btn-clear {
  background-color: #e50000;
  color: white;
  border: 2px outset #e50000;
}

.btn-save {
  background-color: lightgray;
  border: 2px outset lightgray;
}

.slider {
  -webkit-appearance: none;
  height: 15px;
  border-radius: 5px;  
  background: darkgray;
  outline: none;
  opacity: 0.7;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%; 
  background: greenyellow;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: greenyellow;
  cursor: pointer;
}
img {
  border: 5px ridge goldenrod;
  width: 400px;
  height: auto;
  background-color: white;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  text-align: center;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
  margin: 0px 20px 0px 20px;
}

h1 {
  text-align: center;
  font-size: 50px;
}
